import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import mixpanel from 'mixpanel-browser';

import { languages, difficulties } from '../asset/data';

const WordMatchClear = ({ level, language, difficulty, timeElapsed, userName, setUserName }) => {
    const navigate = useNavigate();

    const languageDisplay = languages.find(lang => lang.value === language)?.display || language;
    const difficultyDisplay = difficulties.find(diff => diff.value === difficulty)?.display || difficulty;

    const handleMixpanelSubmit = () => {
        mixpanel.track('WordGameLanking', {
            'Level': level,
            'Language': language,
            'Difficulty': difficulty,
            'User Name': userName,
            'Time Elapsed': timeElapsed
        });
        alert("   !");
    };

    const handleTwitterShare = () => {
      mixpanel.track('WordGameShare', {
        'Level': level,
        'Language': language,
        'Difficulty': difficulty,
        'User Name': userName,
        'Time Elapsed': timeElapsed,
        'SNS': 'Twitter'
    });
        const twitterUrl = `https://twitter.com/intent/tweet?text=${encodeURIComponent(
            ` ${level}  ${languageDisplay}  ${difficultyDisplay}  ${timeElapsed}  ! https://zerda217.github.io`
        )}`;
        window.open(twitterUrl, '_blank');
    };

    const handleRestart = () => {
      mixpanel.track('WordGameRestart', {
        'User Name': userName,
      });
      navigate('/word')
    }

    return (
        <Wrap>
            <TitleContainer>
                <Circle>{level} </Circle>
                <Circle>{languageDisplay}</Circle>
                <Circle> {difficultyDisplay}</Circle>
            </TitleContainer>
            <Wrap>
                <Input
                    type="text"
                    value={userName}
                    onChange={(e) => setUserName(e.target.value)}
                    placeholder=" "
                />
                <Button onClick={handleMixpanelSubmit}> </Button>
                <Button onClick={handleTwitterShare}> </Button>
                <Button onClick={handleRestart}> </Button>
            </Wrap>
        </Wrap>
    )
};

const Wrap = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
`;

const TitleContainer = styled.div`
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
`;

const Circle = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1VH;
  border-radius: 25px;
  margin: 5px;
  background-color: #E2F5EB;
`;

const Input = styled.input`
  padding: 10px;
  margin: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
`;

const Button = styled.button`
  padding: 10px 20px;
  margin: 10px;
  border: none;
  border-radius: 5px;
  background-color: #1D6F42;
  color: white;
  cursor: pointer;
  &:hover {
    background-color: #155b32;
  }
`;

export default WordMatchClear;
